<template>
  <section class="section">
    <div class="container mt-5">
      <div class="row">
        <div class="col-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-6 offset-lg-3 col-xl-4 offset-xl-4">
          <div class="login-brand">
            <img src="../assets/img/impact-assesment.svg" alt="logo" width="200" >
          </div>

          <div class="card card-primary">
            <div class="card-header"><h4>Reset Password</h4></div>

            <div class="card-body">
              <p class="text-muted">Please fill this form with your New Password</p>
                <div class="form-group">
                  <label for="password">New Password</label>
                  <input v-model="password" id="password" type="password" class="form-control" name="password" tabindex="1" required autofocus>
                </div>
                <div class="form-group">
                  <label for="confirm_password">Confirm New Password</label>
                  <input v-model="confirm_password" id="confirm_password" type="password" class="form-control" name="confirm_password" tabindex="1" required autofocus>
                </div>

                <div class="form-group">
                  <button v-on:click="resetPassword" type="submit" class="btn btn-primary btn-lg btn-block" tabindex="4">
                    Update Password
                  </button>
                </div>
            </div>
          </div>
          <div class="simple-footer">
            Copyright &copy; Impact Management 2023
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: 'ResetPassword',
  data()
  {
    return {
      password:"",
      confirm_password:"",
    }
  },
  methods:{
    async resetPassword(){
      if (this.password == "" || this.confirm_password == "") {
        this.showNullSwal();
        return;
      }

      if (this.password != this.confirm_password) {
        this.showNotMatchSwal();
        return;
      }

      const otp = localStorage.getItem('otp');
      let email = localStorage.getItem('email');
      email = email.slice(1, -1);
      let result = await axios.post(process.env.VUE_APP_BASE_URL + '/auth/reset-password', {
        otp: otp,
        email: email,
        password: this.password,
        confirm_password: this.confirm_password
      }, {
        headers: {}
      });

      if (result.status == 200) {
        this.showSuccessSwal();
        localStorage.removeItem('otp');
        localStorage.removeItem('email');
        this.$router.push({name: "Login"})
      }else{
        this.showErrorSwal(result.data.message);
      }
    },
    showNullSwal(){
      Swal.fire({
        icon: 'error',
        title: 'Error!',
        text: 'Please fill all the form!',
        confirmButtonText: 'Ok'
      })
    },
    showNotMatchSwal(){
      Swal.fire({
        icon: 'error',
        title: 'Error!',
        text: 'Password and Confirm Password not match!',
        confirmButtonText: 'Ok'
      })
    },
    showSuccessSwal(){
      Swal.fire({
        title: 'Reset Password',
        text: 'Reset Password Success',
        icon: 'success',
        confirmButtonText: 'Ok'
      })
    },
    showErrorSwal($message){
      Swal.fire({
        title: 'Error!',
        text: $message,
        icon: 'error',
        confirmButtonText: 'Ok'
      })
    }
  }
}
</script>