      }else if ( this.globalProfiling[0].year + 1 == this.currentYear ){
        this.warningYearProfiling = false;
      }else{
        this.warningYearProfiling = true;
        this.warningYear = this.globalProfiling[0].year + 1;
        this.newProfilingYear = this.warningYear;
        this.getDetailProfilingToCreateNew(this.globalProfiling[0].id);
      }
    },
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    formatNumber(number) {
      // Convert number to string and split it into parts of 3 digits
      const parts = number.toString().split('.');
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ".");
      // Join the parts and return
      return parts.join('.');
    },
    menuProfiling($event){
      this.menuprofiling = $event;
