    isWizard($event){
      if (this.wizard >= $event) {
        return true;
      }
        return false;
    },
    isProfiling($event){
      if (this.wizard == $event) {
        return true;
      }
        return false;
    },
    reversValue($variable){
      return !$variable;
    },
