    },
    async getProfilingDetail($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/community-profilings/" + $id, config);
        if (response.status == 200) {
          this.detailYear = response.data.year;
          this.detailVillage = response.data.village.name;
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    errorSwal($event){
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: $event,
      });
    },
    successSwal($event){
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: $event,
      });
    },
    successToast($event){
      const Toast = Swal.mixin({
        toast: true,
        position: 'bottom-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer)
          toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
      });
      Toast.fire({
        icon: 'success',
        title: $event
      });
    },
  },
}
</script>

