<template>
    <div id="login">
    <section class="section">
      <div class="container mt-5">
        <div class="row">
          <div class="col-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-6 offset-lg-3 col-xl-4 offset-xl-4">
            <div class="login-brand">
              <img src="../assets/img/impact-assesment.svg" alt="logo" width="200" >
            </div>

            <div class="card card-primary">
              <div class="card-header"><h4>Login</h4></div>

              <div class="card-body">
                <div v-if="required" class="alert alert-danger alert-dismissible show fade">
                      <div class="alert-body">
                        <button v-on:click="reset" class="close" data-dismiss="alert">
                          <span>&times;</span>
                        </button>
                        Email and Password is Required!
                      </div>
                    </div>
                    <div v-if="unauth" class="alert alert-danger alert-dismissible show fade">
                      <div class="alert-body">
                        <button v-on:click="resetauth" class="close" data-dismiss="alert">
                          <span>&times;</span>
                        </button>
                        Credential Not Found!
                      </div>
                    </div>
                <form @submit.prevent="login" class="needs-validation" novalidate="">
                  <div class="form-group">
                    <label for="email">Email</label>
                    <input v-model="email" id="email" type="email" class="form-control" name="email" tabindex="1" required autofocus>
                    <div class="invalid-feedback">
                      Please fill in your email
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="d-block">
                    <label for="password" class="control-label">Password</label>
                      <div class="float-right">
                        <a v-on:click="forgot" href="#" class="text-small">
                          Forgot Password?
                        </a>
                      </div>
                    </div>
                    <input v-model="password" id="password" type="password" class="form-control" name="password" tabindex="2" required>
                    <div class="invalid-feedback">
                      please fill in your password
                    </div>
                  </div>

                  <div class="form-group">
                    <button type="submit" class="btn btn-primary btn-lg btn-block" tabindex="4">
                      Login
                    </button>
                  </div>
                </form>

              </div>
            </div>
            <div class="simple-footer">
              Copyright &copy; Impact Management 2023
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";
export default {
  name: 'LogIn',
  data()
  {
    return {
        email:"",
        password:"",
        required: false,
        unauth: false,
    }
  },
  methods:{
    async login()
    {
        if (this.email == "" || this.password == "") {
          this.showErrorNullSwal();
        } else {
          let result = await axios.post(process.env.VUE_APP_BASE_URL + '/auth/login', {
            email: this.email,
            password: this.password
        },{
        headers: {
        }
        })

        if(result.status==200) {
            this.showSuccessSwal();
            localStorage.setItem("token", JSON.stringify(result.data.data.token.toString()))
            this.$router.push({name: 'Home'})
        } else {
          this.showErrorSwal(result.data.message);
        }
        }
    },
    reset(){
      this.required = false;
    },
    resetauth(){
      this.unauth = false;
    },
    forgot(){
      this.$router.push({name: "ForgotPassword"})
    },
    showSuccessSwal(){
      Swal.fire({
        title: 'Login Success',
        text: 'You are successfully logged in',
        icon: 'success',
        confirmButtonText: 'OK',
        timer: 5000
      })
    },
    showErrorSwal($mess){
      Swal.fire({
        title: 'Error!',
        text: $mess,
        icon: 'error',
        confirmButtonText: 'OK',
        timer: 5000
      })
    },
    showErrorNullSwal(){
      Swal.fire({
        title: 'Error!',
        text: 'Email and Password is Required!',
        icon: 'error',
        confirmButtonText: 'OK',
        timer: 5000
      })
    },
  }
}
</script>