<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>Issue Category Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>Category List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <p>Show
                          <select class="custom-select col-2">
                            <option selected value="5">5</option>
                            <option value="10">10</option>
                            <option value="50">50</option>
                          </select>
                          Entries</p>
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <button class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add Category</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Category Name</th>
                          <th>Description</th>
                          <th>Action</th>
                        </tr>
                        <tr v-if="Array.isArray(categories) && categories.length === 0">
                          <td colspan="4" class="text-center">No Data Found</td>
                        </tr>
                        <tr v-for="(item, index) in categories" :key="item.id">
                          <td>{{ index + 1 }}</td>
                          <td>{{ item.name }}</td>
                          <td>{{ item.description }}</td>
                          <td>
                            <a v-on:click="getCategory(item.id)" style="margin: 5px;" href="#" class="btn btn-info" data-toggle="modal" data-target="#detailModal"><i class="fas fa-info-circle"></i></a>
                            <a v-on:click="getCategory(item.id)" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="confirmSwal(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Create Category Issue</h5>
          <button v-on:click="refreshCategory()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Category Name</label>
            <input v-model="category.name" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Description Category</label>
            <textarea v-model="category.description" style="height: 121px;" type="text" class="form-control"></textarea>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshCategory()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="addCategory()" type="button" class="btn btn-primary" data-dismiss="modal">Add Category</button>
        </div>
      </div>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Update Category Issue</h5>
          <button v-on:click="refreshCategory()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Category Name</label>
            <input v-model="category.name" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Description Category</label>
            <textarea v-model="category.description" style="height: 121px;" type="text" class="form-control"></textarea>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshCategory()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="updateCategory(category.id)" type="button" class="btn btn-primary" data-dismiss="modal">Save</button>
        </div>
      </div>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="detailModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Detail Category Issue</h5>
          <button v-on:click="refreshCategory()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <table class="table table-bordered table-md">
          <tr>
            <th>Category Name</th>
            <td>{{ category.name }}</td>
          </tr>
          <tr>
            <th>Description</th>
            <td>{{ category.description }}</td>
          </tr>
        </table>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshCategory()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
export default {
  name: 'IssueCategoryManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      categories: [],
      category: [],
    };
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
    this.getCategories();
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    refreshCategory(){
      this.category = [];
    },
    async getCategories(){
      let token = localStorage.getItem('token');
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const response = await axios.get(process.env.VUE_APP_BASE_URL +'/issue-category', config);
      if (response.status == 200) {
        this.categories = response.data.data;
      }
    },
    async addCategory(){
      let token = localStorage.getItem('token');
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const response = await axios.post(process.env.VUE_APP_BASE_URL +'/issue-category', {
        name: this.category.name,
        description: this.category.description,
      },config);
      if (response.status == 200) {
        this.successSwal(response.data.message);
        this.getCategories();
      }
    },
    async updateCategory($id){
      let token = localStorage.getItem('token');
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const response = await axios.post(process.env.VUE_APP_BASE_URL +'/issue-category/update/' + $id, {
        name: this.category.name,
        description: this.category.description,
      },config);
      if (response.status == 200) {
        this.getCategories();
        this.refreshCategory();
        this.successSwal(response.data.message);
      }
    },
    async deleteCategory($id){
      let token = localStorage.getItem('token');
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const response = await axios.delete(process.env.VUE_APP_BASE_URL +'/issue-category/' + $id, config);
      if (response.status == 200) {
        this.successSwal(response.data.message);
        this.getCategories();
      }
    },
    async getCategory($id){
      let token = localStorage.getItem('token');
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const response = await axios.get(process.env.VUE_APP_BASE_URL +'/issue-category/' + $id, config);
      if (response.status == 200) {
        this.category = response.data;
      }
    },
    successSwal($mess){
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: $mess,
      })
    },
    confirmSwal($id){
      Swal.fire({
        title: 'Are you sure?',
        text: "You will delete this category issue!",
        icon: 'warning',
        showCancelButton: true,
        cancelButtonText: 'No, keep it',
        confirmButtonText: 'Yes, delete it!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteCategory($id);
        } else if (
          result.dismiss === Swal.DismissReason.cancel
        ) {
          Swal.fire(
            'Cancelled',
            'Your category file is safe :)',
            'success'
          )
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

