import LogIn from "./pages/Login.vue";
import Home from "./pages/Dashboard.vue";
import UserManagement from "./pages/UserManagement.vue";
import RoleManagement from "./pages/RoleManagement.vue";
import ProfilePage from "./pages/Profile.vue";
import IssueIdentification from "./pages/IssueIdentification.vue";
import ForgotPassword from "./pages/ForgotPassword.vue";
import OTPVerification from "./pages/OTPVerification.vue";
import ResetPassword from "./pages/ResetPassword.vue";
import { createRouter, createWebHistory } from "vue-router";
import EntityManagement from "@/pages/EntityManagement.vue";
import IssueMatrix from "@/pages/IssueMatrix.vue";
import IssueCategoryManagement from "@/pages/IssueCategoryManagement.vue";
import ProvinceManagement from "@/pages/ProvinceManagement.vue";
import CityManagement from "@/pages/CityManagement.vue";
import DistrictManagement from "@/pages/DistrictManagement.vue";
import VillageManagement from "@/pages/VillageManagement.vue";
import CommunityProfilling from "@/pages/CommunityProfilling.vue";
import StakeholderManagement from "@/pages/StakeholderManagement.vue";
import StakeholderProfiling from "@/pages/StakeholderProfiling.vue";
import IssueAnalysis from "@/pages/IssueAnalysis.vue";
import IssueSolution from "@/pages/IssueSolution.vue";
import IssueImplementation from "@/pages/IssueImplementation.vue";
import IssueEvaluation from "@/pages/IssueEvaluation.vue";
import DashboardIssue from "@/pages/DashboardIssue.vue";
import DashboardCommunity from "@/pages/DashboardCommunity.vue";
import PermissionManagement from "@/pages/PermissionManagement.vue";

const routes = [
  {
    name: "Login",
    component: LogIn,
    path: "/",
  },
  {
    name: "Home",
    component: Home,
    path: "/dashboard-general",
  },
  {
    name: "UserManagement",
    component: UserManagement,
    path: "/user-management"
  },
  {
    name: "RoleManagement",
    component: RoleManagement,
    path: "/role-management"
  },
  {
    name: "Profile",
    component: ProfilePage,
    path: "/profile"
  },
  {
    name: "IssueIdentification",
    component: IssueIdentification,
    path: "/issue-identification"
  },
  {
    name: "ForgotPassword",
    component: ForgotPassword,
    path: "/forgot-password"
  },
  {
    name: "OTPVerification",
    component: OTPVerification,
    path: "/otp-verification"
  },
  {
    name: "ResetPassword",
    component: ResetPassword,
    path: "/reset-password"
  },
  {
    name: "EntityManagement",
    component: EntityManagement,
    path: "/entity-management"
  },
  {
    name: "IssueMatrix",
    component: IssueMatrix,
    path: "/issue-matrix"},
  {
    name: "IssueCategory",
    component: IssueCategoryManagement,
    path: "/issue-category"
  },
  {
    name: "ProvinceManagement",
    component: ProvinceManagement,
    path: "/provinces"
  },
  {
    name: "CityManagement",
    component: CityManagement,
    path: "/cities"
  },
  {
    name: "DistrictManagement",
    component: DistrictManagement,
    path: "/districts"
  },
  {
    name: "VillageManagement",
    component: VillageManagement,
    path: "/villages"
  },
  {
    name: "CommunityProfilling",
    component: CommunityProfilling,
    path: "/community-profilling"
  },
  {
    name: "StakeholderManagement",
    component: StakeholderManagement,
    path: "/stakeholders"
  },
  {
    name: "StakeholderProfiling",
    component: StakeholderProfiling,
    path: "/stakeholder-profiling"
  },
  {
    name: "IssueAnalysis",
    component: IssueAnalysis,
    path: "/issue-analysis"
  },
  {
    name: "IssueSolution",
    component: IssueSolution,
    path: "/issue-solution"
  },
  {
    name: "IssueImplementation",
    component: IssueImplementation,
    path: "/issue-implementation"
  },
  {
    name: "IssueEvaluation",
    component: IssueEvaluation,
    path: "/issue-evaluation"
  },
  {
    name: "DashboardIssue",
    component: DashboardIssue,
    path: "/dashboard-issue"
  },
  {
    name: "DashboardCommunity",
    component: DashboardCommunity,
    path: "/dashboard-community"
  },
  {
    name: "PermissionManagement",
    component: PermissionManagement,
    path: "/permission-management"
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
