<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>District Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>District List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <p>Show
                          <select class="custom-select col-2">
                            <option selected value="5">5</option>
                            <option value="10">10</option>
                            <option value="50">50</option>
                          </select>
                          Entries</p>
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <button v-on:click="getProvinces()" class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add District</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Province Name</th>
                          <th>City Name</th>
                          <th>District Name</th>
                          <th>Action</th>
                        </tr>
                        <tr v-if="Array.isArray(districts) && districts.length === 0">
                          <td colspan="5" class="text-center">No Data Found</td>
                        </tr>
                        <tr v-for="(item, index) in districts" :key="item.id">
                          <td>{{ index + 1 }}</td>
                          <td>{{ item.province.name }}</td>
                          <td>{{ item.city.name }}</td>
                          <td>{{ item.name }}</td>
                          <td>
                            <a v-on:click="getDistrict(item.id)" style="margin: 5px;" href="#" class="btn btn-info" data-toggle="modal" data-target="#detailModal"><i class="fas fa-info-circle"></i></a>
                            <a v-on:click="getDistrict(item.id)" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="confirmDelete(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Create District</h5>
            <button v-on:click="refreshDistrict()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label>Province</label>
              <v-select v-on:click="selectedCity = [], cities = []" v-model="selectedProvince" label="name" value="id" :options="provinces"></v-select>
            </div>
            <div class="form-group">
              <label>City</label>
              <v-select v-model="selectedCity" label="name" value="id"
                      :options="cities" v-on:click="getCities(selectedProvince.id)"></v-select>
            </div>
            <div class="form-group">
              <label>District Name</label>
              <input v-model="district.name" type="text" class="form-control">
            </div>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshDistrict()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button v-on:click="addDistrict()" type="button" class="btn btn-primary" data-dismiss="modal">Add District</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Update District</h5>
            <button v-on:click="refreshDistrict()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label>Province</label>
              <v-select v-model="selectedProvince" label="name" value="id"
                      :options="provinces" v-on:click="getProvinces()"></v-select>
            </div>
            <div class="form-group">
              <label>City</label>
              <v-select v-model="selectedCity" label="name" value="id"
                      :options="cities" v-on:click="getCities(selectedProvince.id)"></v-select>
            </div>
            <div class="form-group">
              <label>District Name</label>
              <input v-model="district.name" type="text" class="form-control">
            </div>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshDistrict()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button v-on:click="updateDistrict()" type="button" class="btn btn-primary" data-dismiss="modal">Update District</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="detailModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Detail District</h5>
            <button v-on:click="refreshDistrict()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <table class="table table-bordered table-md">
              <tr>
                <th>Province Name</th>
                <td>{{ detailProvince.name }}</td>
              </tr>
              <tr>
                <th>City Name</th>
                <td>{{ detailCity.name }}</td>
              </tr>
              <tr>
                <th>District Name</th>
                <td>{{ district.name }}</td>
              </tr>
            </table>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshDistrict()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
export default {
  name: 'RoleManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      districts: [],
      district: [],
      selectedProvince: [],
      selectedCity: [],
      provinces: [],
      cities: [],
      detailProvince: [],
      detailCity: [],
    };
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
    this.getDistricts();
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    refreshDistrict(){
      this.district = [];
      this.selectedProvince = [];
      this.selectedCity = [];
      this.detailCity = [];
      this.detailProvince = [];
    },
    async getDistricts($bool = true) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/districts", config);
        if (response.status == 200) {
          this.districts = response.data;
          if($bool){
            this.successToast("Districts loaded successfully");
          }
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getDistrict(id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      await axios.get(process.env.VUE_APP_BASE_URL +"/districts/"+id, config)
      .then(response => {
        this.district = response.data;
        this.selectedProvince = response.data.province;
        this.selectedCity = response.data.city;
        this.detailProvince = response.data.province;
        this.detailCity = response.data.city;
      })
      .catch(error => {
        this.errorSwal(error);
      });
    },
    async getProvinces() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/provinces", config);
        if (response.status == 200) {
          this.provinces = response.data;
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getCities($id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/cities/province/" + $id, config);
        if (response.status == 200) {
          this.cities = response.data;
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async addDistrict() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const data = {
        name: this.district.name,
        city_id: this.selectedCity.id,
        province_id: this.selectedProvince.id
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/districts", data, config);
        if (response.status == 201) {
          this.successSwal(response.data.message);
          this.getDistricts(false);
          this.refreshDistrict();
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async deleteDistrict($id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.delete(process.env.VUE_APP_BASE_URL +"/districts/"+$id, config);
        if (response.status == 201) {
          this.successSwal(response.data.message);
          this.getDistricts(false);
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async updateDistrict() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const data = {
        name: this.district.name,
        city_id: this.selectedCity.id,
        province_id: this.selectedProvince.id
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/districts/update/"+this.district.id, data, config);
        if (response.status == 201) {
          this.successSwal(response.data.message);
          this.getDistricts(false);
          this.refreshDistrict();
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    errorSwal($message) {
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: $message,
      })
    },
    successSwal($message) {
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: $message,
      })
    },
    successToast($mess){
      const Toast = Swal.mixin({
        toast: true,
        position: 'bottom-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer)
          toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
      })
      Toast.fire({
        icon: 'success',
        title: $mess
      })
    },
    confirmDelete($id){
      Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, delete it!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteDistrict($id);
        }else if (
          result.dismiss === Swal.DismissReason.cancel
        ) {
          Swal.fire(
            'Cancelled',
            'Your District file is safe :)',
            'success'
          )
        }
      })
    }
  },
}
</script>

