<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>Role Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>Role List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <p>Show
                          <select class="custom-select col-2">
                            <option selected value="5">5</option>
                            <option value="10">10</option>
                            <option value="50">50</option>
                          </select>
                          Entries</p>
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <button v-on:click="getPermissions()" class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add Role</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Role Name</th>
                          <th>Detail Permission</th>
                          <th>Action</th>
                        </tr>
                        <tr v-for="(item, index) in roles" :key="item.id">
                          <td>{{ index + 1 }}</td>
                          <td>{{ item.name }}</td>
                          <td><a v-on:click="getDetailRole(item.id)" href="#" class="btn btn-info" data-toggle="modal" data-target="#detailModal"><i class="fas fa-search"></i></a></td>
                          <td>
                            <a v-on:click="beforeUpdate(item.id)" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="confirmSwal(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Add Role</h5>
          <button v-on:click="refreshInputRole()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Role Name </label>
            <input v-model="inputRoleName" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label class="d-block">Permission</label>
            <v-select multiple :options="permissionOptions" label="slug" value="id" v-model="permissionSelected" />
            <br>
            <div class="alert alert-light alert-dismissible show fade">
              <div class="alert-body">
                  Please Select <b>Module</b> To Showing Menu on Sidebar!
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshInputRole()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="addRole()" type="button" class="btn btn-primary" data-dismiss="modal">Create</button>
        </div>
      </div>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="detailModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Detail Role</h5>
          <button v-on:click="refreshRole()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="label">
            <p><strong>Role Name</strong> : {{ role.name }}</p>
          </div>
          <div class="label">
            <p><strong>Permission</strong> : </p>
            <div class="table-responsive">
              <table class="table table-bordered table-md">
                <tr>
                  <th>#</th>
                  <th>Permission</th>
                </tr>
                <tr v-for="(item, index) in rolePermissions" :key="item.id">
                  <td>{{ index + 1 }}</td>
                  <td>{{ item.permission.name }}</td>
                </tr>
              </table>
            </div>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshRole()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
        </div>
      </div>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Edit Role</h5>
          <button v-on:click="refreshRole()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Role Name </label>
            <input v-model="role.name" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label class="d-block">Permission</label>
            <v-select multiple :options="permissionOptions" label="slug" value="id" v-model="permissionSelected" />
            <br>
            <div class="alert alert-light alert-dismissible show fade">
              <div class="alert-body">
                  Please Select <b>Module</b> To Showing Menu on Sidebar!
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshRole()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="updateRole(role.id)" type="button" class="btn btn-primary" data-dismiss="modal">Update</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: 'RoleManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      roles: [],
      role: [],
      updatePermission: [],
      rolePermissions: [],
      inputRoleName: "",
      permissionSelected: [],
      permissionOptions: [],
    };
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
    this.getRoles();
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    beforeUpdate($id){
      this.getDetailRole($id);
      this.getPermissions();
    },
    async getRoles() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
      let result = await axios.get(process.env.VUE_APP_BASE_URL + "/role", config);
      if (result.status == 200) {
        this.roles = result.data;
      } else {
        this.roles = [];
      }
    },
    async getDetailRole($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
      let result = await axios.get(process.env.VUE_APP_BASE_URL + "/role/" + $id, config);
      if (result.status == 200) {
        this.role = result.data.role;
        this.rolePermissions = result.data.permissions;
        for (let i = 0; i < this.rolePermissions.length; i++) {
          this.permissionSelected.push(this.rolePermissions[i].permission);
        }
      } else {
        this.role = [];
      }
    },
    refreshRole(){
      this.role = [];
      this.permissionSelected = [];
      this.rolePermissions = [];
    },
    refreshInputRole(){
      this.inputRoleName = "";
      this.permissionSelected = [];
    },
    async addRole(){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
      let result = await axios.post(process.env.VUE_APP_BASE_URL + "/role", {
        name: this.inputRoleName,
        permission: this.permissionSelected.map(permission => permission.id),
      }, config);
      if (result.status == 200) {
        this.getRoles();
        this.refreshInputRole();
        this.successToast(result.data.message);
      } else {
        this.warningToast(result.data.name);
      }
    },
    async deleteRole($roleId){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
      let result = await axios.delete(process.env.VUE_APP_BASE_URL + "/role/" + $roleId, config);
      if (result.status == 200) {
        this.getRoles();
        this.successToast(result.data.message);
      } else {
        this.warningToast(result.data.message);
      }
    },
    async updateRole($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
      let result = await axios.post(process.env.VUE_APP_BASE_URL + "/role/update/" + $id, {
        name: this.role.name,
        permission: this.permissionSelected.map(permission => permission.id),
      }, config);
      if (result.status == 200) {
        this.getRoles();
        this.successToast(result.data.message);
        this.refreshRole();
      } else {
        this.warningToast(result.data.message);
      }
    },
    successToast($message){
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: $message,
      })
    },
    warningToast($message){
      Swal.fire({
        icon: 'warning',
        title: 'Oops...',
        text: $message,
      })
    },
    confirmSwal($id){
      Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, Delete it!',
        cancelButtonText: 'No, Cancel!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteRole($id);
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          Swal.fire(
            'Cancelled',
            'Your role is safe :)',
            'success',
          )
        }
      })
    },
    async getPermissions(){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
      let result = await axios.get(process.env.VUE_APP_BASE_URL + "/permission", config);
      if (result.status == 200) {
        this.permissionOptions = result.data;
      } else {
        this.permissionOptions = [];
      }
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

