<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>Stakeholder Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>Stakeholder List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <p>Show
                          <select class="custom-select col-2">
                            <option selected value="5">5</option>
                            <option value="10">10</option>
                            <option value="50">50</option>
                          </select>
                          Entries</p>
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <button v-on:click="getStakeholderCategory()" class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add Stakeholder</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Stakeholder Name</th>
                          <th>Stakeholder Category</th>
                          <th>Action</th>
                        </tr>
                        <tr v-if="Array.isArray(stakeholders) && stakeholders.length === 0">
                          <td colspan="4" class="text-center">No Data Found</td>
                        </tr>
                        <tr v-for="(item, index) in stakeholders" :key="item.id">
                          <td>{{ index + 1 }}</td>
                          <td>{{ item.name }}</td>
                          <td>{{ item.stakeholder_category.name }}</td>
                          <td>
                            <a v-on:click="getDetailStakeholder(item.id)" style="margin: 5px;" href="#" class="btn btn-info" data-toggle="modal" data-target="#detailModal"><i class="fas fa-info-circle"></i></a>
                            <a v-on:click="getDetailStakeholder(item.id), getStakeholderCategory()" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="confirmDelete(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Create Stakeholder</h5>
            <button v-on:click="refreshStakeholder()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label>Stakeholder Name</label>
              <input v-model="stakeholder.name" type="text" class="form-control">
            </div>
            <div class="form-group">
              <label>Stakeholder Phone Number</label>
              <input v-model="stakeholder.phone_number" type="text" class="form-control">
            </div>
            <div class="form-group">
              <label>Stakeholder Category</label>
              <v-select v-model="selectedCategory" label="name" value="id"
                      :options="categories"></v-select>
            </div>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshStakeholder()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button v-on:click="addStakeholder()" type="button" class="btn btn-primary" data-dismiss="modal">Add Stakeholders</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Update Stakeholder</h5>
            <button v-on:click="refreshStakeholder()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label>Stakeholder Name</label>
              <input v-model="stakeholder.name" type="text" class="form-control">
            </div>
            <div class="form-group">
              <label>Stakeholder Phone Number</label>
              <input v-model="stakeholder.phone_number" type="text" class="form-control">
            </div>
            <div class="form-group">
              <label>Stakeholder Category</label>
              <v-select v-model="selectedCategoryUpdate" label="name" value="id"
                      :options="categories"></v-select>
            </div>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshStakeholder()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button v-on:click="updateStakeholder()" type="button" class="btn btn-primary" data-dismiss="modal">Save</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="detailModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Detail Stakeholder</h5>
            <button v-on:click="refreshStakeholder()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <table class="table table-md">
              <tr>
                <td>Stakeholder Name</td>
                <td>{{ stakeholder.name }}</td>
              </tr>
              <tr>
                <td>Stakeholder Phone Number</td>
                <td>{{ stakeholder.phone_number }}</td>
              </tr>
              <tr>
                <td>Stakeholder Category</td>
                <td>{{ stakeholderCategory.name }}</td>
              </tr>
            </table>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshStakeholder()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" class="btn btn-primary" data-dismiss="modal">Add District</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: 'StakeholderManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      stakeholders: [],
      categories: [],
      selectedCategory: [],
      stakeholder: [],
      stakeholderCategory: [],
      selectedCategoryUpdate: []
    };
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
    this.getStakeholders();
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    async getStakeholders($bool = true) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/stakeholders", config);
        if (response.status == 200) {
          this.stakeholders = response.data;
          if($bool){
            this.successToast("Stakeholders loaded successfully");
          }
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getStakeholderCategory() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/stakeholder-categories", config);
        if (response.status == 200) {
          this.categories = response.data;
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async addStakeholder() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/stakeholders", {
          name: this.stakeholder.name,
          phone_number: this.stakeholder.phone_number,
          stakeholder_category_id: this.selectedCategory.id
        }, config);
        if (response.status == 201) {
          this.successSwal(response.data.message);
          this.refreshStakeholder();
          this.getStakeholders(false);
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getDetailStakeholder($id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/stakeholders/"+$id, config);
        if (response.status == 200) {
          this.stakeholder = response.data;
          this.stakeholderCategory = response.data.stakeholder_category;
          this.selectedCategoryUpdate = response.data.stakeholder_category;
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async updateStakeholder() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/stakeholders/update/"+this.stakeholder.id, {
          name: this.stakeholder.name,
          phone_number: this.stakeholder.phone_number,
          stakeholder_category_id: this.selectedCategoryUpdate.id
        }, config);
        if (response.status == 201) {
          this.successSwal(response.data.message);
          this.refreshStakeholder();
          this.getStakeholders(false);
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async deleteStakeholder($id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.delete(process.env.VUE_APP_BASE_URL +"/stakeholders/"+$id, config);
        if (response.status == 201) {
          this.successSwal(response.data.message);
          this.getStakeholders(false);
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    refreshStakeholder(){
      this.stakeholder = [];
      this.selectedCategory = [];
      this.categories = [];
      this.stakeholderCategory = [];
      this.selectedCategoryUpdate = [];
    },
    successSwal(message) {
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: message,
      });
    },
    errorSwal(message) {
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: message,
      });
    },
    successToast(message) {
      const Toast = Swal.mixin({
        toast: true,
        position: 'bottom-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer)
          toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
      })
      Toast.fire({
        icon: 'success',
        title: message
      })
    },
    confirmDelete($id) {
      Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, delete it!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteStakeholder($id);
        }else if (
          result.dismiss === Swal.DismissReason.cancel
        ) {
          Swal.fire(
            'Cancelled',
            'Your stakeholder file is safe :)',
            'success'
          )
        }
      })
    }
  },
}
</script>

