<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>User Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>User List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <!-- <p>Show 
                          <select class="custom-select col-2">
                        <option selected value="5">5</option>
                        <option value="10">10</option>
                        <option value="50">50</option>
                      </select>
                          Entries</p> -->
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <!-- <button class="btn btn-info" data-toggle="modal" data-target="#importModal"><i class="fas fa-solid fa-file-import"></i> Import User</button> -->
                          <button v-on:click="beforeAdd()" class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add User</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Name</th>
                          <th>Email</th>
                          <th>Role</th>
                          <th>Status</th>
                          <th>Action</th>
                        </tr>
                        <tr v-for="(item, index) in user" :key="item.id">
                          <td>{{ index + 1}}</td>
                          <td>{{ item.name }}</td>
                          <td>{{ item.email }}</td>
                          <td>{{ item.role.name }}</td>
                          <td>
                            <div v-if="item.userstatus.name == 'Active' " class="badge badge-success">Active</div>
                            <div v-if="item.userstatus.name == 'Suspended' " class="badge badge-danger">Suspend</div>
                          </td>

                          <td>
                            <a v-on:click="getDetail(item.id)" style="margin: 5px;" href="#" class="btn btn-info" data-toggle="modal" data-target="#detailModal"><i class="fas fa-info-circle"></i></a>
                            <a v-on:click="getUpdate(item.id)" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="confirmDelete(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <!-- <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav> -->
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="detailModal">
          <!-- <div class="modal-dialog modal-dialog-centered" style="max-width: 85%;" role="document"> -->
          <div class="modal-dialog modal-md modal-dialog-centered" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title">Detail User</h5>
                <button v-on:click="deleteDetail" type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <p><strong>Name : </strong>{{  detail.name }}</p>
                <p><strong>Email : </strong>{{  detail.email }}</p>
                <p><strong>Phone : </strong> {{ detail.phone_number }}</p>
                <p><strong>Role : </strong>
                  <span class="badge badge-info">{{ detailRole.name }}</span>
                </p>
                <p><strong>Status : </strong>
                  <span v-if="detailStatus.name == 'Active'" class="badge badge-success">{{ detailStatus.name }}</span>
                  <span v-if="detailStatus.name == 'Suspended'" class="badge badge-danger">{{ detailStatus.name }}</span>
                </p>
                <p><strong>Entitas </strong></p>
                <ul>
                  <li v-for="entity in detailEntities" :key="entity">{{ entity.entity.name }}</li>
                </ul>
              </div>
              <div class="modal-footer bg-whitesmoke br">
                <button v-on:click="deleteDetail" type="button" class="btn btn-primary" data-dismiss="modal">Close</button>
              </div>
            </div>
          </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
          <!-- <div class="modal-dialog modal-dialog-centered" style="max-width: 85%;" role="document"> -->
          <div class="modal-dialog modal-md modal-dialog-centered" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title">Update User</h5>
                <button v-on:click="deleteUpdate()" type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div class="modal-body">
                <div class="card-body">
                    <div class="form-group row">
                      <label for="inputName3" class="col-sm-3 col-form-label">Name</label>
                      <div class="col-sm-9">
                        <input v-model="edit.name" type="text" class="form-control" id="inputName3" name="name">
                      </div>
                    </div>
                    <div class="form-group row">
                      <label for="inputEmail3" class="col-sm-3 col-form-label">Email</label>
                      <div class="col-sm-9">
                        <input v-model="edit.email" type="email" class="form-control" id="inputEmail3" name="email">
                      </div>
                    </div>
                    <div class="form-group row">
                      <label for="inputNumber3" class="col-sm-3 col-form-label">Phone Number</label>
                      <div class="col-sm-9">
                        <input v-model="edit.phone_number" type="text" class="form-control" id="inputNumber3" name="number">
                      </div>
                    </div>
                    <div class="form-group row">
                      <label for="inputEmail3" class="col-sm-3 col-form-label">Status</label>
                      <div class="col-sm-9">
                        <v-select v-model="edit.userstatus" :options="editStatusOptions" label="name" value="id" ></v-select>
                      </div>
                    </div>
                    <div class="form-group row">
                      <label for="inputEmail3" class="col-sm-3 col-form-label">Role</label>
                      <div class="col-sm-9">
                        <v-select v-model="editRole" :options="editRoleOptions" label="name" value="id" ></v-select>
                      </div>
                    </div>
                    <div class="form-group row">
                      <label for="inputEmail3" class="col-sm-3 col-form-label">Entity</label>
                      <div class="col-sm-9">
                        <v-select v-model="editEntity2" :options="editEntityOptions" label="name" value="id" multiple=""></v-select>
                      </div>
                    </div>
                  </div>
              </div>
              <div class="modal-footer bg-whitesmoke br">
                <button v-on:click="deleteUpdate()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                <button v-on:click="updateUser(edit.id)" type="button" class="btn btn-primary" data-dismiss="modal">Save changes</button>
              </div>
            </div>
          </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="importModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Import User</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="custom-file">
            <input type="file" class="custom-file-input" id="customFile">
            <label class="custom-file-label" for="customFile">Choose file</label>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button type="button" class="btn btn-primary" data-dismiss="modal">Import</button>
        </div>
      </div>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Create User</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Name </label>
            <input v-model="newName" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Email </label>
            <input v-model="newEmail" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Phone Number </label>
            <input v-model="newPhone" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Password </label>
            <input v-model="newPassword" type="password" class="form-control">
          </div>
          <div class="form-group">
            <label>Role</label>
            <v-select v-model="newRole" label="name" value="id" :options="roles"></v-select>
          </div>
          <div class="form-group">
            <label>Entity</label>
            <v-select v-model="selectedEntities" label="name" value="id"
                      :options="entities"
                      multiple></v-select>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="addUser()" type="button" class="btn btn-primary" data-dismiss="modal">Add User</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from "sweetalert2";
export default {
  name: 'UserManagement',
  data() {
    return {
      scriptElements: [],
      user: [],
      roles: [],
      entities: [],
      selectedEntities: null,
      detail: [],
      detailRole: [],
      detailStatus: [],
      detailEntities: [],
      newName: '',
      newEmail: '',
      newPhone: '',
      newPassword: '',
      newRole: null,
      newEntity: [],
      edit: [],
      editRole: null,
      editRoleOptions: [],
      editEntityOptions: [],
      editEntity: [],
      editEntity2: [],
      editStatusOptions: [
        { id: 1, name: 'Active' },
        { id: 2, name: 'Suspended' },
      ],
      // editSelectedRole: null,
    };
  },
  computed: {
    // entity() {
    //   return this.getEntity();
    // }
  },
  mounted() {
    this.getUser();
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    async getUser() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
      let result = await axios.get(process.env.VUE_APP_BASE_URL+'/users', config)
      if (result.status == 200) {
        this.user = result.data.data;
      }
    },
    async getDetail(id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
      };
      let result = await axios.get(process.env.VUE_APP_BASE_URL+'/users/'+id, config)
      if (result.status == 200) {
        this.detail = result.data.data;
        this.detailRole = result.data.data.role;
        this.detailStatus = result.data.data.userstatus;
        this.detailEntities = result.data.entity;
      }
    },
    async getUpdate(id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      };
      let result = await axios.get(process.env.VUE_APP_BASE_URL+'/users/'+id, config)
      if (result.status == 200) {
        this.edit = result.data.data;
        this.editEntity = result.data.entity;
        for (let i = 0; i < this.editEntity.length; i++) {
          this.editEntity2.push(this.editEntity[i].entity);
        }
        this.beforeUpdate();
      }
    },
    deleteDetail(){
      this.detail = [];
      this.detailRole = [];
      this.detailStatus = [];
      this.detailEntities = [];
    },
    deleteUpdate(){
      this.edit = [];
      this.editRole = null;
      this.editRoleOptions = [];
      this.editEntityOptions = [];
      this.editEntity = [];
      this.editEntity2 = [];
    },
    async updateUser(id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      let result = await axios.post(process.env.VUE_APP_BASE_URL+'/users/update/'+id, {
            name: this.edit.name,
            email: this.edit.email,
            phone_number: this.edit.phone_number,
            role_id: this.editRole.id,
            userstatus_id: this.edit.userstatus.id,
            entity: this.editEntity2.map(entity => entity.id)
        },{
        headers: {
          Authorization: `Bearer ${token}`,
        }
      })
      if (result.status == 200) {
        this.getUser();
        this.addSuccessSwal(result.data.message);
        this.deleteUpdate();
      }
    },
    async confirmDelete( $id ) {
      const result = await Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "Yes, delete it!",
      });

      if (result.value) {
        this.deleteUser($id);
      }
    },
    async deleteUser(id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      let result = await axios.delete(process.env.VUE_APP_BASE_URL+'/users/'+id, {
        headers: {
          Authorization: `Bearer ${token}`,
        }
      },{})
      if (result.status == 200) {
        this.getUser();
        this.addSuccessSwal(result.data.message);
      }
    },
    beforeAdd(){
      this.getRole();
      this.getEntity();
    },
    beforeUpdate(){
      this.getRoleOptions();
      this.selectedRole();
      this.getEntityOptions();
    },
    async getRole(){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
      };
      let result = await axios.get(process.env.VUE_APP_BASE_URL+'/role', config)
      if (result.status == 200) {
        this.roles = result.data;
      } else {
        return [];
      }
    },
    async getEntity(){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
      };
      let result = await axios.get(process.env.VUE_APP_BASE_URL+'/entity', config)
      if (result.status == 200) {
        this.entities =  result.data.data;
      } else {
        return [];
      }
    },

    async addUser(){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      let result = await axios.post(process.env.VUE_APP_BASE_URL+'/users', {
            name: this.newName,
            email: this.newEmail,
            phone_number: this.newPhone,
            password: this.newPassword,
            role_id: this.newRole.id,
            entity: this.selectedEntities.map(entity => entity.id)
        },{
        headers: {
          Authorization: `Bearer ${token}`,
        }
      })
      if (result.status == 200) {
        this.addSuccessSwal(result.data.message);
        this.getUser();
        this.newName = '';
        this.newEmail = '';
        this.newPhone = '';
        this.newPassword = '';
        this.newRole = null;
        this.selectedEntities = null;
      }
    },
    addSuccessSwal ( $mess ){
      Swal.fire({
        title: 'Success',
        text: $mess,
        icon: 'success',
        confirmButtonText: 'Ok',
      })
    },
    selectedRole (){
      this.editRole = this.edit.role;
    },
    async getRoleOptions(){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
      };
      let result = await axios.get(process.env.VUE_APP_BASE_URL+'/role', config)
      if (result.status == 200) {
        this.editRoleOptions = result.data;
      } else {
        return [];
      }
    },
    async getEntityOptions (){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
      };
      let result = await axios.get(process.env.VUE_APP_BASE_URL+'/entity', config)
      if (result.status == 200) {
        this.editEntityOptions =  result.data.data;
      } else {
        return [];
      }
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

