<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>Village Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>Village List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <p>Show
                          <select class="custom-select col-2">
                            <option selected value="5">5</option>
                            <option value="10">10</option>
                            <option value="50">50</option>
                          </select>
                          Entries</p>
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <button v-on:click="getProvinces()" class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add Village</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Province Name</th>
                          <th>City Name</th>
                          <th>District Name</th>
                          <th>Village Name</th>
                          <th>Action</th>
                        </tr>
                        <tr v-if="Array.isArray(villages) && villages.length === 0">
                          <td colspan="6" class="text-center">No Data Found</td>
                        </tr>
                        <tr v-for="(item, index) in villages" :key="item.id">
                          <td>{{ index + 1 }}</td>
                          <td>{{ item.province.name }}</td>
                          <td>{{ item.city.name }}</td>
                          <td>{{ item.district.name }}</td>
                          <td>{{ item.name }}</td>
                          <td>
                            <a v-on:click="getVillage(item.id)" style="margin: 5px;" href="#" class="btn btn-info" data-toggle="modal" data-target="#detailModal"><i class="fas fa-info-circle"></i></a>
                            <a v-on:click="getForUpdate(item.id), getProvinces()" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="confirmDelete(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Create Village</h5>
            <button v-on:click="refreshVillage()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label>Province</label>
              <v-select v-model="selectedProvince" label="name" value="id"
                      :options="provinces" v-on:click="cities = [], selectedCity = [], districts = [], selectedDistrict = []"></v-select>
            </div>
            <div class="form-group">
              <label>City</label>
              <v-select v-model="selectedCity" label="name" value="id"
                      :options="cities" v-on:click="getCity(selectedProvince.id), districts = [], selectedDistrict = []"></v-select>
            </div>
            <div class="form-group">
              <label>District</label>
              <v-select v-model="selectedDistrict" label="name" value="id"
                      :options="districts" v-on:click="getDistrict(selectedCity.id)"></v-select>
            </div>
            <div class="form-group">
              <label>Village Name</label>
              <input v-model="village.name" type="text" class="form-control">
            </div>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshVillage()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button v-on:click="addVillage()" type="button" class="btn btn-primary" data-dismiss="modal">Add Village</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Update Village</h5>
            <button v-on:click="refreshVillage()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label>Province</label>
              <v-select v-model="uptSelectedProvince" label="name" value="id"
                      :options="provinces" v-on:click="cities = [], uptSelectedCity = [], districts = [], uptSelectedDistrict = []"></v-select>
            </div>
            <div class="form-group">
              <label>City</label>
              <v-select v-model="uptSelectedCity" label="name" value="id"
                      :options="cities" v-on:click="getCity(uptSelectedProvince.id), districts = [], uptSelectedDistrict = []"></v-select>
            </div>
            <div class="form-group">
              <label>District</label>
              <v-select v-model="uptSelectedDistrict" label="name" value="id"
                      :options="districts" v-on:click="getDistrict(uptSelectedCity.id)"></v-select>
            </div>
            <div class="form-group">
              <label>Village Name</label>
              <input v-model="uptVillage.name" type="text" class="form-control">
            </div>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshVillage()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button v-on:click="updateVillage(uptVillage.id)" type="button" class="btn btn-primary" data-dismiss="modal">Update Village</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="detailModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Detail Village</h5>
            <button v-on:click="refreshVillage()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <table class="table table-bordered table-md">
              <tr>
                <th>Province Name</th>
                <td>{{ detailProvince.name }}</td>
              </tr>
              <tr>
                <th>City Name</th>
                <td>{{ detailCity.name }}</td>
              </tr>
              <tr>
                <th>District Name</th>
                <td>{{ detailDistrict.name }}</td>
              </tr>
              <tr>
                <th>Village Name</th>
                <td>{{ village.name }}</td>
              </tr>
            </table>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshVillage()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Swal from 'sweetalert2';
export default {
  name: 'RoleManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      provinces: [],
      selectedProvince: [],
      cities: [],
      selectedCity: [],
      districts: [],
      selectedDistrict: [],
      village: [],
      villages: [],
      detailProvince: [],
      detailCity: [],
      detailDistrict: [],
      uptVillage: [],
      uptSelectedProvince: [],
      uptSelectedCity: [],
      uptSelectedDistrict: []
    };
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
    this.getVillages();
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    refreshVillage() {
      this.village = [];
      this.selectedProvince = [];
      this.selectedCity = [];
      this.selectedDistrict = [];
      this.detailCity = [];
      this.detailDistrict = [];
      this.detailProvince = [];
      this.uptVillage = [];
      this.uptSelectedProvince = [];
      this.uptSelectedCity = [];
      this.uptSelectedDistrict = [];
    },
    async updateVillage($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/villages/update/" + $id, {
          name: this.uptVillage.name,
          district_id: this.uptSelectedDistrict.id,
          city_id: this.uptSelectedCity.id,
          province_id: this.uptSelectedProvince.id
        }, config);
        if (response.status == 201) {
          this.successSwal(response.data.message);
          this.getVillages(false);
          this.refreshVillage();
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getForUpdate($id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/villages/" + $id, config);
        if (response.status == 200) {
          this.uptVillage = response.data;
          this.uptSelectedProvince = response.data.province;
          this.uptSelectedCity = response.data.city;
          this.uptSelectedDistrict = response.data.district;
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getVillages($bool = true) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/villages", config);
        if (response.status == 200) {
          this.villages = response.data;
          if($bool){
            this.successToast("Villages loaded successfully");
          }
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getProvinces() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/provinces", config);
        if (response.status == 200) {
          this.provinces = response.data;
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getCity(provinceId) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/cities/province/" + provinceId, config);
        if (response.status == 200) {
          this.cities = response.data;
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getDistrict(cityId) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/districts/city/" + cityId, config);
        if (response.status == 200) {
          this.districts = response.data;
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async addVillage() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/villages", {
          name: this.village.name,
          district_id: this.selectedDistrict.id,
          city_id: this.selectedCity.id,
          province_id: this.selectedProvince.id
        }, config);
        if (response.status == 201) {
          this.successSwal(response.data.message);
          this.getVillages(false);
          this.refreshVillage();
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getVillage($id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/villages/" + $id, config);
        if (response.status == 200) {
          this.village = response.data;
          this.detailProvince = response.data.province;
          this.detailCity = response.data.city;
          this.detailDistrict = response.data.district;
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async deleteVillage($id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.delete(process.env.VUE_APP_BASE_URL +"/villages/" + $id, config);
        if (response.status == 201) {
          this.successSwal(response.data.message);
          this.getVillages(false);
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    successSwal(message) {
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: message,
      });
    },
    errorSwal(message) {
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: message,
      });
    },
    successToast(message) {
      const Toast = Swal.mixin({
        toast: true,
        position: 'bottom-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer)
          toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
      })
      Toast.fire({
        icon: 'success',
        title: message
      })
    },
    confirmDelete($id) {
      Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, delete it!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteVillage($id);
        }else if (
          result.dismiss === Swal.DismissReason.cancel
        ) {
          Swal.fire(
            'Cancelled',
            'Your village file is safe :)',
            'success'
          )
        }
      })
    }
  },
}
</script>

