<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>Province Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>Province List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <p>Show
                          <select class="custom-select col-2">
                            <option selected value="5">5</option>
                            <option value="10">10</option>
                            <option value="50">50</option>
                          </select>
                          Entries</p>
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <button class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add Province</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Province Name</th>
                          <th>Action</th>
                        </tr>
                        <tr v-if="Array.isArray(provinces) && provinces.length === 0">
                          <td colspan="3" class="text-center">No Data Found</td>
                        </tr>
                        <tr v-for="(item, index) in provinces" :key="item.id">
                          <td>{{ index + 1 }}</td>
                          <td>{{ item.name }}</td>
                          <td>
                            <a v-on:click="getProvince(item.id)" style="margin: 5px;" href="#" class="btn btn-info" data-toggle="modal" data-target="#detailModal"><i class="fas fa-info-circle"></i></a>
                            <a v-on:click="getProvince(item.id)" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="confirmDeleteSwal(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Create Province</h5>
            <button v-on:click="refreshProvince()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label>Province Name</label>
              <input v-model="province.name" type="text" class="form-control">
            </div>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshProvince()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button v-on:click="addProvince()" type="button" class="btn btn-primary" data-dismiss="modal">Add Province</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="detailModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Detail Province</h5>
            <button v-on:click="refreshProvince()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <table class="table table-bordered table-md">
              <tr>
                <th>Province Name</th>
                <td>{{ province.name }}</td>
              </tr>
            </table>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshProvince()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Update Province</h5>
            <button v-on:click="refreshProvince()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label>Province Name</label>
              <input v-model="province.name" type="text" class="form-control">
            </div>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshProvince()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button v-on:click="updateProvince()" type="button" class="btn btn-primary" data-dismiss="modal">Update Province</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: 'ProvinceManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      provinces: [],
      province: [],
    };
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
    this.getProvinces();
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    async getProvinces() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/provinces", config);
        this.provinces = response.data;
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async addProvince() {
      if (this.province.name == null) {
        this.errorSwal("Name is mandatory field!");
        return;
      }
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/provinces", 
        {
          name: this.province.name}
        , config);
        if (response.status == 201) {
          this.successSwal(response.data.message);
          this.getProvinces();
          this.refreshProvince();
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getProvince(id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +`/provinces/${id}`, config);
        this.province = response.data;
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async updateProvince() {
      if (this.province.name == null) {
        this.errorSwal("Name is mandatory field!");
        return;
      }
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +`/provinces/update/${this.province.id}`, 
        {
          name: this.province.name}
        , config);
        if (response.status == 200) {
          this.successSwal(response.data.message);
          this.getProvinces();
          this.refreshProvince();
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async deleteProvince(id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.delete(process.env.VUE_APP_BASE_URL +`/provinces/${id}`, config);
        if (response.status == 200) {
          this.successSwal(response.data.message);
          this.getProvinces();
        }
      } catch (error) {
        this.errorSwal(error);
      }
    },
    refreshProvince() {
      this.province = [];
    },
    successSwal($message) {
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: $message,
      })
    },
    errorSwal($message) {
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: $message,
      })
    },
    confirmDeleteSwal($id){
      Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, delete it!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteProvince($id);
        } else if (
          result.dismiss === Swal.DismissReason.cancel
        ) {
          Swal.fire(
            'Cancelled',
            'Your province file is safe :)',
            'success'
          )
        }
      })
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

