<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>Permission Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>Permission List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <p>Show
                          <select class="custom-select col-2">
                            <option selected value="5">5</option>
                            <option value="10">10</option>
                            <option value="50">50</option>
                          </select>
                          Entries</p>
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <button class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add Role</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Permission Slug</th>
                          <th>Permission Name</th>
                          <th>Action</th>
                        </tr>
                        <tr v-for="(item, index) in permissions" :key="item.id">
                          <td>{{ index + 1 }}</td>
                          <td>{{ item.slug }}</td>
                          <td>{{ item.name }}</td>
                          <td>
                            <a v-on:click="detailPermission(item.id)" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="confirmDelete(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Add Permission</h5>
          <button v-on:click="refreshNewPermission()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Permission Slug </label>
            <input v-model="newPermissionSlug" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Permission Name </label>
            <input v-model="newPermissionName" type="text" class="form-control">
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshNewPermission()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="addPermission()" type="button" class="btn btn-primary" data-dismiss="modal">Create</button>
        </div>
      </div>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Update Permission</h5>
          <button v-on:click="refreshNewPermission()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Permission Slug </label>
            <input v-model="permission.slug" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Permission Name </label>
            <input v-model="permission.name" type="text" class="form-control">
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshNewPermission()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="updatePermission(permission.id)" type="button" class="btn btn-primary" data-dismiss="modal">Update</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: 'PermissionManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      permissions: [],
      newPermissionName: "",
      newPermissionSlug: "",
      permission: [],
    };
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
    this.getPermission();
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    async getPermission() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
        let result = await axios.get(process.env.VUE_APP_BASE_URL + "/permission", config);
        if (result.status == 200) {
          this.permissions = result.data;
        }
    },
    refreshNewPermission() {
      this.newPermissionName = "";
      this.newPermissionSlug = "";
      this.permission = [];
    },
    async addPermission() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
        let result = await axios.post(process.env.VUE_APP_BASE_URL + "/permission", {
          name: this.newPermissionName,
          slug: this.newPermissionSlug,
        }, config);
        if (result.status == 200) {
          this.getPermission();
          this.refreshNewPermission();
          this.successToast(result.data.message);
        }
    },
    async deletePermission($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
        let result = await axios.delete(process.env.VUE_APP_BASE_URL + "/permission/" + $id, config);
        if (result.status == 200) {
          this.getPermission();
          this.successToast(result.data.message);
        }
    },
    async detailPermission($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
        let result = await axios.get(process.env.VUE_APP_BASE_URL + "/permission/" + $id, config);
        if (result.status == 200) {
          this.permission = result.data;
        }
    },
    async updatePermission($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
        let result = await axios.post(process.env.VUE_APP_BASE_URL + "/permission/update/" + $id, {
          name: this.permission.name,
          slug: this.permission.slug,
        }, config);
        if (result.status == 200) {
          this.getPermission();
          this.successToast(result.data.message);
          this.refreshNewPermission();
        }
    },
    successToast($message){
      Swal.fire({
        title: 'Success',
        text: $message,
        icon: 'success',
        confirmButtonText: 'OK',
      })
    },
    confirmDelete($id){
      Swal.fire({
        title: 'Are you sure?',
        text: "You will delete this permission",
        icon: 'warning',
        showCancelButton: true,
        cancelButtonText: 'No, keep it',
        confirmButtonText: 'Yes, delete it!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          this.deletePermission($id);
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          Swal.fire(
            'Cancelled',
            'Your permission is safe :)',
            'success',
          )
        }
      })
    }
  },
}
</script>

