<template>
  <section class="section">
    <div class="container mt-5">
      <div class="row">
        <div class="col-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-6 offset-lg-3 col-xl-4 offset-xl-4">
          <div class="login-brand">
            <img src="../assets/img/impact-assesment.svg" alt="logo" width="200" >
          </div>

          <div class="card card-primary">
            <div class="card-header"><h4>Forgot Password</h4></div>

            <div class="card-body">
              <div v-if="required" class="alert alert-danger alert-dismissible show fade">
                <div class="alert-body">
                  <button v-on:click="reset" class="close" data-dismiss="alert">
                    <span>&times;</span>
                  </button>
                  Email is Required!
                </div>
              </div>
              <div v-if="unauthEmail" class="alert alert-danger alert-dismissible show fade">
                <div class="alert-body">
                  <button v-on:click="resetUnauthEmail" class="close" data-dismiss="alert">
                    <span>&times;</span>
                  </button>
                  Email Not Found!
                </div>
              </div>
              <p class="text-muted">We will send One Time Password to reset your password</p>
                <div class="form-group">
                  <label for="email">Email</label>
                  <input v-model="email" id="email" type="email" class="form-control" name="email" tabindex="1" required autofocus>
                </div>

                <div class="form-group">
                  <button v-on:click="otp" type="submit" class="btn btn-primary btn-lg btn-block" tabindex="4">
                    Send OTP
                  </button>
                </div>
            </div>
          </div>
          <div class="simple-footer">
            Copyright &copy; Impact Management 2023
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
export default {
  name: 'ForgotPassword',
  data()
  {
    return {
      email:"",
      required: false,
      unauthEmail: false,
    }
  },
  methods:{
    async otp(){
      if (this.email == "") {
        this.required = true;
      } else {
        let result = await axios.post(process.env.VUE_APP_BASE_URL + '/auth/forgot-password', {
          email: this.email
        },{
          headers: {
          }
        })

        if(result.status==200) {
          this.showSuccessSwal();
          this.$router.push({name: "OTPVerification"})
        } else if (result.status == 203) {
          this.unauthEmail = true;
        }
      }
    },
    reset(){
      this.required = false;
    },
    resetUnauthEmail(){
      this.unauthEmail = false;
    },
    showSuccessSwal(){
      Swal.fire({
        title: 'OTP Verification',
        text: 'We have sent One Time Password to your email',
        icon: 'success',
        confirmButtonText: 'OK'
      })
    },
  }
}
</script>