<template>
  <section class="section">
    <div class="container mt-5">
      <div class="row">
        <div class="col-12 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-6 offset-lg-3 col-xl-4 offset-xl-4">
          <div class="login-brand">
            <img src="../assets/img/impact-assesment.svg" alt="logo" width="200" >
          </div>

          <div class="card card-primary">
            <div class="card-header"><h4>OTP Verification</h4></div>

            <div class="card-body">
              <p class="text-muted">Please fill this form with your One Time Password</p>
                <div class="form-group">
                  <label for="otp">One Time Password</label>
                  <input v-model="otp" id="otp" type="number" class="form-control" name="otp" tabindex="1" required autofocus>
                </div>

                <div class="form-group">
                  <button v-on:click="verification" type="submit" class="btn btn-primary btn-lg btn-block" tabindex="4">
                    Verification OTP
                  </button>
                </div>
            </div>
          </div>
          <div class="simple-footer">
            Copyright &copy; Impact Management 2023
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
export default {
  name: 'OTPVerification',
  data()
  {
    return {
      otp:"",
    }
  },
  methods:{
    async verification(){
      if (this.otp == "") {
        this.showNullSwal();
        return;
      }else {
        let result = await axios.post(process.env.VUE_APP_BASE_URL + '/auth/otp-verification', {
          otp: this.otp
        }, {
          headers: {
            'Content-Type': 'application/json',
          }
        });

        if (result.status == 200) {
          localStorage.setItem('otp', JSON.stringify(result.data.otp));
          localStorage.setItem('email', JSON.stringify(result.data.email));
          this.showSuccessSwal();
          this.$router.push({name: "ResetPassword"})
        }else{
          this.showErrorSwal(result.data.message);
        }
      }

      // this.$router.push({name: "ResetPassword"})
    },
    showNullSwal() {
      Swal.fire({
        title: 'Error!',
        text: 'One Time Password is Required!',
        icon: 'error',
        confirmButtonText: 'Ok'
      })
    },
    showSuccessSwal(){
      Swal.fire({
        title: 'OTP Verification',
        text: 'OTP Verification Success',
        icon: 'success',
        confirmButtonText: 'Ok'
      })
    },
    showErrorSwal($message){
      Swal.fire({
        title: 'Error!',
        text: $message,
        icon: 'error',
        confirmButtonText: 'Ok'
      })
    }
  }
}
</script>