<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>Entity Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>Entity List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <p>Show
                          <select class="custom-select col-2">
                            <option selected value="5">5</option>
                            <option value="10">10</option>
                            <option value="50">50</option>
                          </select>
                          Entries</p>
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <button class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add Entity</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Logo</th>
                          <th>Entity Name</th>
                          <th>Entity Address</th>
                          <th>Action</th>
                        </tr>
                        <tr v-for="(item, index) in entities" :key="item.id">
                          <td>{{ index + 1 }}</td>
                          <td>
                            <img :src="item.logo" style="max-width: 150px; max-height: 150px;">
                          </td>
                          <td>{{ item.name }}</td>
                          <td>{{ item.address }}</td>
                          <td>
                            <a v-on:click="detailEntity(item.id)" style="margin: 5px;" href="#" class="btn btn-info" data-toggle="modal" data-target="#detailModal"><i class="fas fa-info-circle"></i></a>
                            <a v-on:click="detailEntity(item.id)" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="confirmSwal(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Create Entity</h5>
          <button v-on:click="refreshEntity()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
              <label>Entity Logo</label>
              <div class="custom-file">
                <input v-on:change="handleLogoUpload" type="file" class="custom-file-input" id="customFile">
                <label class="custom-file-label" for="customFile">Choose File</label>
              </div>
              <div v-if="selectedFile">
                <br>
                <img :src="filePreview" alt="File Preview" style="max-width: 150px; max-height: 150px;" />
              </div>
          </div>
          <div class="form-group">
              <label>Entity Name</label>
              <input v-model="entity.name" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Entity Address</label>
            <input v-model="entity.address" type="text" class="form-control">
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshEntity()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="addEntity()" type="button" class="btn btn-primary" data-dismiss="modal">Add Entity</button>
        </div>
      </div>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Update Entity</h5>
          <button v-on:click="refreshEntity()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Current Logo</label>
            <br>
            <img :src="entity.logo" alt="Current Logo" style="max-width: 150px; max-height: 150px;">
          </div>
          <div class="form-group">
              <label>Entity Logo</label>
              <div class="custom-file">
                <input v-on:change="handleLogoUpload" type="file" class="custom-file-input" id="customFile">
                <label class="custom-file-label" for="customFile">Choose File</label>
              </div>
              <div v-if="selectedFile">
                <br>
                <label>New Logo</label>
                <br>
                <img :src="filePreview" alt="File Preview" style="max-width: 150px; max-height: 150px;" />
              </div>
          </div>
          <div class="form-group">
            <label>Entity Name</label>
            <input v-model="entity.name" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Entity Address</label>
            <input v-model="entity.address" type="text" class="form-control">
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshEntity()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="updateEntity(entity.id)" type="button" class="btn btn-primary" data-dismiss="modal">Save</button>
        </div>
      </div>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="detailModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Detail Entity</h5>
          <button v-on:click="refreshEntity()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="label">
            <label><strong>Entity Logo</strong></label>
            <br>
            <img :src="entity.logo" style="max-width: 150px; max-height: 150px;">
          </div>
          <br>
          <div class="label">
            <label><strong>Entity Name</strong></label>
            <p>{{ entity.name }}</p>
          </div>
          <div class="label">
            <label><strong>Entity Address</strong></label>
            <p>{{ entity.address }}</p>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshEntity()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
export default {
  name: 'EntityManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      entities: [],
      entity: [],
      selectedFile: null,
      filePreview: null,
    };
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
    this.getEntities();
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    async getEntities() {
      let token = localStorage.getItem('token');
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const response = await axios.get(process.env.VUE_APP_BASE_URL + '/entity', config);
      if (response.status == 200) {
        this.entities = response.data.data;
      }
    },
    async detailEntity($id){
      let token = localStorage.getItem('token');
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const response = await axios.get(process.env.VUE_APP_BASE_URL + '/entity/' + $id, config);
      if (response.status == 200) {
        this.entity = response.data;
      }
    },
    refreshEntity(){
      this.entity = [];
      this.selectedFile = null;
    },
    async addEntity(){
      let token = localStorage.getItem('token');
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const formData = new FormData();
      formData.append('name', this.entity.name);
      formData.append('address', this.entity.address);
      formData.append('logo', this.selectedFile);

      const response = await axios.post(process.env.VUE_APP_BASE_URL + '/entity', 
        formData, config);
      if (response.status == 200) {
        this.refreshEntity();
        this.getEntities();
        this.successSwal(response.data.message);
      }
    },
    async deleteEntity($id){
      let token = localStorage.getItem('token');
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      const response = await axios.delete(process.env.VUE_APP_BASE_URL + '/entity/' + $id, config);
      if (response.status == 200) {
        this.successSwal(response.data.message);
        this.getEntities();
      }
    },
    async updateEntity($id){
      let token = localStorage.getItem('token');
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };

      if (this.selectedFile) {
        const formData = new FormData();
        formData.append('name', this.entity.name);
        formData.append('address', this.entity.address);
        formData.append('logo', this.selectedFile);
        const response = await axios.post(process.env.VUE_APP_BASE_URL + '/entity/update/' + $id, 
          formData, config);
        if (response.status == 200) {
          this.refreshEntity();
          this.getEntities();
          this.successSwal(response.data.message);
        }
      }else{
        const response = await axios.post(process.env.VUE_APP_BASE_URL + '/entity/update/' + $id, 
        {
          name: this.entity.name,
          address: this.entity.address,
        }, config);
        if (response.status == 200) {
          this.refreshEntity();
          this.getEntities();
          this.successSwal(response.data.message);
        }
      }
    },
    successSwal($mess){
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: $mess,
      })
    },
    confirmSwal($id){
      Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        cancelButtonText: 'No, keep it',
        confirmButtonText: 'Yes, delete it!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteEntity($id);
        } else if (
          result.dismiss === Swal.DismissReason.cancel
        ) {
          Swal.fire(
            'Cancelled',
            'Your entity file is safe :)',
            'success'
          )
        }
      })
    },
    handleLogoUpload(event){
      this.selectedFile = event.target.files[0];
      console.log(this.selectedFile);
      if (this.selectedFile) {
        const reader = new FileReader();
        reader.onload = () => {
          this.filePreview = reader.result;
        };
        reader.readAsDataURL(this.selectedFile);
      } else {
        this.filePreview = null;
      }
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

