<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>Issue Matrix Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>Issue Matrix List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <p>Show
                          <select class="custom-select col-2">
                            <option selected value="5">5</option>
                            <option value="10">10</option>
                            <option value="50">50</option>
                          </select>
                          Entries</p>
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <button class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add Matrix</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Matrix Name</th>
                          <th>Matrix Description</th>
                          <th>Action</th>
                        </tr>
                        <tr v-if="Array.isArray(matrixs) && matrixs.length === 0">
                          <td colspan="4" class="text-center">No Data Found</td>
                        </tr>
                        <tr v-for="(item, index) in matrixs" :key="item.id">
                          <td>{{ index + 1 }}</td>
                          <td>{{ item.name }}</td>
                          <td>{{ item.description }}</td>
                          <td>
                            <a v-on:click="detailMatrix(item.id)" style="margin: 5px;" href="#" class="btn btn-info" data-toggle="modal" data-target="#detailModal"><i class="fas fa-info-circle"></i></a>
                            <a v-on:click="detailMatrix(item.id)" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="swalConfirm(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Create Matrix Issue</h5>
          <button v-on:click="refreshMatrix()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Matrix Name</label>
            <input v-model="matrix.name" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Matrix Description</label>
            <textarea v-model="matrix.description" style="height: 121px;" type="text" class="form-control"></textarea>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshMatrix()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="addMatrix()" type="button" class="btn btn-primary" data-dismiss="modal">Add Matrix</button>
        </div>
      </div>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Update Matrix Issue</h5>
          <button v-on:click="refreshMatrix()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Matrix Name</label>
            <input v-model="matrix.name" type="text" class="form-control">
          </div>
          <div class="form-group">
            <label>Matrix Description</label>
            <textarea v-model="matrix.description" style="height: 121px;" type="text" class="form-control"></textarea>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshMatrix()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          <button v-on:click="updateMatrix(matrix.id)" type="button" class="btn btn-primary" data-dismiss="modal">Save</button>
        </div>
      </div>
    </div>
  </div>
  <div class="modal fade" tabindex="-1" role="dialog" id="detailModal">
    <div class="modal-dialog modal-md modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Detail Matrix Issue</h5>
          <button v-on:click="refreshMatrix()" type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label>Matrix Name</label> <p> {{ matrix.name }}</p>
          </div>
          <div class="form-group">
            <label>Matrix Description</label> <p> {{ matrix.description }}</p>
          </div>
        </div>
        <div class="modal-footer bg-whitesmoke br">
          <button v-on:click="refreshMatrix()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
export default {
  name: 'IssueMatrixManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      matrixs: [],
      matrix: [],
    };
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
    this.getMatrixs();
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    async getMatrixs() {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/issue-matrix", config);
        this.matrixs = response.data.data;
      } catch (error) {
        console.error(error);
      }
    },
    refreshMatrix(){
      this.matrix = [];
    },
    async addMatrix(){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/issue-matrix", {
          name: this.matrix.name,
          description: this.matrix.description,
        }, config);
        if(response.status == 200){
          this.getMatrixs();
          this.refreshMatrix();
          this.swalSuccess(response.data.message);
        }
      } catch (error) {
        console.error(error);
      }
    },
    async detailMatrix($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/issue-matrix/" + $id , config);
        this.matrix = response.data;
      } catch (error) {
        console.error(error);
      }
    },
    async updateMatrix($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/issue-matrix/update/" + $id, {
          name: this.matrix.name,
          description: this.matrix.description,
        }, config);
        if(response.status == 200){
          this.getMatrixs();
          this.swalSuccess(response.data.message);
          this.refreshMatrix();
        }
      } catch (error) {
        console.error(error);
      }
    },
    async deleteMatrix($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.delete(process.env.VUE_APP_BASE_URL +"/issue-matrix/" + $id, config);
        if(response.status == 200){
          this.getMatrixs();
          this.swalSuccess(response.data.message);
        }
      } catch (error) {
        console.error(error);
      }
    },
    swalConfirm($id){
      Swal.fire({
        title: 'Are you sure?',
        text: "You will delete this matrix issue!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, delete it!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteMatrix($id);
        } else if (
          result.dismiss === Swal.DismissReason.cancel
        ) {
          Swal.fire(
            'Cancelled',
            'Your category file is safe :)',
            'success'
          )
        }
      })
    },
    swalSuccess($message){
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: $message,
      })
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

