<script>
// import axios from 'axios';
export default {
  name: 'UserManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      user: [],
      DetailId: null,
      url: null,
      DetailName: null,
      DetailEmail: null,
      DetailCreate: null,
      DetailEdit: null,
      entities: [
        'PT Olahkarsa Inovasi Indonesia',
        'PT SBMedia Indonesia',
        'PT Social Responsibility Indonesia'
      ],
      provincies: [
        'Jawa Timur',
        'Jawa Tengah',
        'Jawa Barat'
      ],
      cities: [
        'Bandung',
        'Surabaya',
        'Malang'
      ],
    };
  },
  mounted() {
    // this.getUser();
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    readURL(e) {
      const file = e.target.files[0];
      this.url = URL.createObjectURL(file);
    }
  },
}
</script>

