<template>
    <div class="Navbar">
        <div class="navbar-bg"></div>
      <nav class="navbar navbar-expand-lg main-navbar">
        <form class="form-inline mr-auto">
          <ul class="navbar-nav mr-3">
            <li><a href="#" data-toggle="sidebar" class="nav-link nav-link-lg"><i class="fas fa-bars"></i></a></li>
            <li><a href="#" data-toggle="search" class="nav-link nav-link-lg d-sm-none"><i class="fas fa-search"></i></a></li>
          </ul>
        </form>
        <ul class="navbar-nav navbar-right">
          <li class="dropdown"><a href="#" data-toggle="dropdown" class="nav-link dropdown-toggle nav-link-lg nav-link-user">
            <img alt="image" src="../assets/img/avatar/avatar-1.png" class="rounded-circle mr-1">
            <div class="d-sm-none d-lg-inline-block">Hi, {{ profile }}</div></a>
<!--            <div class="d-sm-none d-lg-inline-block">Hi, Sendy</div></a>-->
            <div class="dropdown-menu dropdown-menu-right">
              <div class="dropdown-title">Logged in {{ email }}</div>
<!--              <div class="dropdown-title">Logged in sendy@mail.com</div>-->
              <a v-on:click="me()" href="#" class="dropdown-item has-icon">
                <i class="far fa-user"></i> Profile
              </a>
              <div class="dropdown-divider"></div>
              <a v-on:click="login()" href="#" class="dropdown-item has-icon text-danger">
                <i class="fas fa-sign-out-alt"></i> Logout
              </a>
            </div>
          </li>
        </ul>
      </nav>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import Swal from 'sweetalert2';
  export default {
    name: 'NavBar',
    props: {
    name: String
    },
    data() {
      return {
        profile: '',
        email:''
      };
    },
    mounted() {
      this.getProfile();
    },
    methods: {
      async login()
      {
        let token = localStorage.getItem('token');
        token = token.slice(1, -1);
        const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
        let logut = await axios.post(process.env.VUE_APP_BASE_URL + '/auth/logout', {}, config);

        if (logut.status == 200) {
          this.showLogoutSwal();
          localStorage.removeItem('token');
          this.$router.push({name:'Login'});
        }
      },
      async me(){
        this.$router.push({name:'Profile'});
      },
      showLogoutSwal(){
        Swal.fire({
          title: 'Logout Success',
          text: 'You are successfully logged out',
          icon: 'success',
          confirmButtonText: 'OK',
        })
      },
      async getProfile(){
        let token = localStorage.getItem('token');
        token = token.slice(1, -1);
        const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };
        await axios.post(process.env.VUE_APP_BASE_URL + '/auth/me', {}, config)
        .then((response) => {
          this.profile = response.data.data.name;
          this.email = response.data.data.email;
        })
        .catch((error) => {
          console.log(error);
          this.$router.push({name:'Login'});
        });
      }
    }
  }
  </script>