<script setup>
  import NavBar from '../components/Navbar.vue';
  import SideBar from '../components/Sidebar.vue';
</script>

<template>
  <div>
    <NavBar/>
    <SideBar/>
    <div class="main-content">
        <section class="section">
          <div class="section-header">
            <h1>City Management</h1>
          </div>
          <div class="section-body">
            <div class="row">
              <div class="col-12 col-md-12 col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4>City List</h4>
                  </div>
                  <div class="card-body">
                    <div class="row">
                      <div class="col-6">
                        <p>Show
                          <select class="custom-select col-2">
                            <option selected value="5">5</option>
                            <option value="10">10</option>
                            <option value="50">50</option>
                          </select>
                          Entries</p>
                      </div>
                      <div class="col-6">
                        <div class="buttons text-right">
                          <button v-on:click="getProvince()" class="btn btn-primary" data-toggle="modal" data-target="#addModal"><i class="fas fa-solid fa-plus"></i> Add City</button>
                        </div>
                      </div>
                    </div>
                    <div class="table-responsive">
                      <table class="table table-bordered table-md">
                        <tr>
                          <th>#</th>
                          <th>Province Name</th>
                          <th>City Name</th>
                          <th>Action</th>
                        </tr>
                        <tr v-if="Array.isArray(cities) && cities.length === 0">
                          <td colspan="4" class="text-center">No Data Found</td>
                        </tr>
                        <tr v-for="(item, index) in cities" :key="item.id">
                          <td>{{ index + 1 }}</td>
                          <td>{{ item.province.name}}</td>
                          <td>{{ item.name }}</td>
                          <td>
                            <a v-on:click="getCity(item.id)" style="margin: 5px;" href="#" class="btn btn-info" data-toggle="modal" data-target="#detailModal"><i class="fas fa-info-circle"></i></a>
                            <a v-on:click="getProvince() && getCity(item.id)" style="margin: 5px;" href="#" class="btn btn-primary" data-toggle="modal" data-target="#updateModal"><i class="far fa-edit"></i></a>
                            <a v-on:click="confirmDeleteSwal(item.id)" style="margin: 5px;" href="#" class="btn btn-danger" data-toggle="modal" data-target="#exampleModal"><i class="fas fa-times"></i></a>
                          </td>
                        </tr>
                      </table>
                    </div>
                  </div>
                  <div class="card-footer text-right">
                    <nav class="d-inline-block">
                      <ul class="pagination mb-0">
                        <li class="page-item disabled">
                          <a class="page-link" href="#" tabindex="-1"><i class="fas fa-chevron-left"></i></a>
                        </li>
                        <li class="page-item active"><a class="page-link" href="#">1 <span class="sr-only">(current)</span></a></li>
                        <li class="page-item">
                          <a class="page-link" href="#">2</a>
                        </li>
                        <li class="page-item"><a class="page-link" href="#">3</a></li>
                        <li class="page-item">
                          <a class="page-link" href="#"><i class="fas fa-chevron-right"></i></a>
                        </li>
                      </ul>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="addModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Create City</h5>
            <button v-on:click="refreshCity()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label>Province</label>
              <v-select v-model="selectedProvince" label="name" value="id"
                      :options="provinces"></v-select>
            </div>
            <div class="form-group">
              <label>City Name</label>
              <input v-model="city.name" type="text" class="form-control">
            </div>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshCity()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button v-on:click="addCity()" type="button" class="btn btn-primary" data-dismiss="modal">Add City</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="updateModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Update City</h5>
            <button v-on:click="refreshCity()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label>Province</label>
              <v-select v-model="selectedProvince" label="name" value="id"
                      :options="provinces"></v-select>
            </div>
            <div class="form-group">
              <label>City Name</label>
              <input v-model="city.name" type="text" class="form-control">
            </div>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshCity()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
            <button v-on:click="updateCity(city.id)" type="button" class="btn btn-primary" data-dismiss="modal">Update City</button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" tabindex="-1" role="dialog" id="detailModal">
      <div class="modal-dialog modal-md modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Detail City</h5>
            <button v-on:click="refreshCity()" type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <table class="table table-bordered table-md">
              <tr>
                <th>Province Name</th>
                <td>{{ province.name }}</td>
              </tr>
              <tr>
                <th>City Name</th>
                <td>{{ city.name }}</td>
              </tr>
            </table>
          </div>
          <div class="modal-footer bg-whitesmoke br">
            <button v-on:click="refreshCity()" type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
export default {
  name: 'CityManagement',
  data() {
    return {
      scriptElements: [], // Simpan referensi elemen-elemen script yang telah ditambahkan
      provinces: [],
      selectedProvince: [],
      city: [],
      cities: [],
      province: [],
    };
  },
  mounted() {
    this.dynamicallyLoadScript("../assets/js/stisla.js");
    this.dynamicallyLoadScript("../assets/js/scripts.js");
    this.dynamicallyLoadScript("../assets/js/custom.js");
    this.getCities();
  },
  beforeUnmount() {
    // Hapus elemen-elemen script sebelum komponen dihancurkan
    this.scriptElements.forEach((scriptElement) => {
      scriptElement.remove();
    });
  },
  methods: {
    dynamicallyLoadScript(url) {
      let script = document.createElement("script");
      script.src = url;
      this.scriptElements.push(script); // Simpan referensi elemen script
      document.body.appendChild(script);
    },
    refreshCity(){
      this.city = [];
      this.selectedProvince = [];
      this.province = [];
    },
    async getCities(){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/cities", config);
        this.cities = response.data;
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getProvince(){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/provinces", config);
        this.provinces = response.data;
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async addCity(){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/cities", {
          name: this.city.name,
          province_id: this.selectedProvince.id
        }, config);
        this.successSwal(response.data.message);
        this.getCities();
        this.refreshCity();
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async deleteCity($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.delete(process.env.VUE_APP_BASE_URL +"/cities/"+$id, config);
        this.successSwal(response.data.message);
        this.getCities();
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async getCity($id) {
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.get(process.env.VUE_APP_BASE_URL +"/cities/"+$id, config);
        this.city = response.data;
        this.province = response.data.province;
        this.selectedProvince = response.data.province;
      } catch (error) {
        this.errorSwal(error);
      }
    },
    async updateCity($id){
      let token = localStorage.getItem("token");
      token = token.slice(1, -1);
      const config = {
        headers: { Authorization: `Bearer ${token}` }
      };
      try {
        const response = await axios.post(process.env.VUE_APP_BASE_URL +"/cities/update/"+ $id, {
          name: this.city.name,
          province_id: this.selectedProvince.id
        }, config);
        this.successSwal(response.data.message);
        this.getCities();
        this.refreshCity();
      } catch (error) {
        this.errorSwal(error);
      }
    },
    successSwal($message) {
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: $message,
      })
    },
    errorSwal($message) {
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: $message,
      })
    },
    confirmDeleteSwal($id){
      Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, delete it!',
        reverseButtons: true
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteCity($id);
        } else if (
          result.dismiss === Swal.DismissReason.cancel
        ) {
          Swal.fire(
            'Cancelled',
            'Your city file is safe :)',
            'success'
          )
        }
      })
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

